/* Fonts */
@import url(https://fonts.googleapis.com/css?family=Orbitron);
@import url(https://fonts.googleapis.com/css?family=Rajdhani);

/* App container */
.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  min-height: 100vh;
}

/* Start header */
.header {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-preferred-size: 200px;
      flex-basis: 200px;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.header__logo {
  margin-right: 10px;
  width: 110px;
}

.header__title,
.header__subtitle {
  color: #fff;
  z-index: 1;
}

.header__title {
  -ms-flex-align: baseline;
      align-items: baseline;
  display: -ms-inline-flexbox;
  display: inline-flex;
  font: .9rem 'Orbitron', sans-serif;  
}

.header__subtitle {
  font: 1.1rem 'Rajdhani', sans-serif;
}
/* --- End header --- */

/* Main */
.main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-pack: center;
      justify-content: center;
  z-index: 1;
}

/* Start planet board */
.planet {
  -ms-flex-item-align: center;
      align-self: center;
  border: 1px solid #fcd000;
  border-radius: 10px;
  color: #fcd000;
  font: .7rem 'Orbitron', sans-serif;
  line-height: 2;
  list-style: none;
  padding: 5px;
  text-align: justify;
}

.planet__name {
  font-size: 1.3rem;
  text-align: center;
}
/* --- End planet board --- */

/* Error message for failed fetch*/
.error {
  -ms-flex-item-align: center;
      align-self: center;
  color: #fcd000;
  font-size: .9rem;
}

/* Bottom container */
.bottom-container {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-preferred-size: 200px;
      flex-basis: 200px;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  z-index: 1;
}

/* Start button */
.btn {
  display: -ms-flexbox;
  display: flex;
}

.btn__image {
  border-radius: 10px;
  border: 1px solid transparent;
  cursor: pointer;
  height: 50px;
  margin-right: 5px;
  outline: none !important;
}

.btn__image:focus {
  border-color: #fff;
}

.btn__text {
  -ms-flex-item-align: end;
      align-self: flex-end;
  color: #fff;
  font: 1rem 'Rajdhani', sans-serif;
}
/* --- End button --- */

/* If the audio does not work it shows the message */
.audio {
  color: #fff;
  padding: 10px;
  z-index: 1;
}

/* Twinkling stars animation */
@-webkit-keyframes twinkling {
  from { background-position: 0 0; }
  to { background-position: -10000px 5000px; }
}
@keyframes twinkling {
  from { background-position: 0 0; }
  to { background-position: -10000px 5000px; }
}

/* Start stars and twinkle backgrounds */
.stars,
.twinkle {
  display: block;
  min-height: 100vh;
  position: fixed;
  width: 100vw;
}

.stars {
  background: #000 url(../images/stars.png) repeat top center;
  z-index: 0;
}

.twinkle {
  -webkit-animation: twinkling 200s linear infinite;
          animation: twinkling 200s linear infinite;
  background: transparent url(../images/twinkle.png) repeat top center;
  z-index: 1;
}
/* --- End stars and twinkle backgrounds --- */

/* Media query */
@media (min-width: 48em) { /* 768px */
  .header__logo {
    width: 150px;
  }

  .header__title {
    font-size: 2rem;
  }

  .header__subtitle {
    font-size: 1.5rem;
  }

  .planet {
    font-size: 1rem;
    padding: 30px;
  }

  .planet__name {
    font-size: 1.5rem;
  }

  .btn__image {
    height: 60px;
  }
}
/* --- End Media query --- */


